$(document).ready(function(){


     // campaign slider
     const campaignSlider = new Swiper('.js-campaign', {
        loop: false,
        speed: 1000,
        navigation: {
            nextEl: ".js-arrow-left",
            prevEl: ".js-arrow-right",
        },
        slidesPerView: 1,
        paginationClickable: true,
        spaceBetween: 20,
        // autoplay: {
        //     delay: 1500,
        // },
        breakpoints: {
            1920: {
                slidesPerView: 4,
                spaceBetween: 24
            },
            1028: {
                slidesPerView: 4,
                spaceBetween: 24
            },
            768: {
                slidesPerView: 2,
                spaceBetween: 10
            },
            480: {
                slidesPerView: 1,
                spaceBetween: 10
            }
        }
    });

    $(".leader .js-cuurent-slide").text(campaignSlider.realIndex + 1);

    $(".leader .js-total-slides").text(Math.floor((($(".js-campaign .swiper-slide").length))))
    campaignSlider.on('slideChange', function () {
        $(".leader .js-cuurent-slide").text(campaignSlider.realIndex + 1);
    });
})